.box {
    margin-top: 60px;
    padding-bottom: 10px;
}

.btn {
    background-color: rgb(95, 95, 145);
    border: 1px solid;
    width: 150px;
    margin-top: 20px;

}
.btn:hover {
    background-color: dodgerblue;
}

h4 {
    margin-top: 10px;
}
h5 {
    margin-top: 20px;
    margin-bottom: -10px;
}
h1 {
    text-align: center;
    margin-top: -5px;
   
}

@media (width < 600px) {
    .btn {
        background-color:  rgb(95, 95, 145);
        border: 1px solid;
        width: 90px;
        margin-top: 30px;
    
    }
    h1 {
       font-size: 20px;
    }
    
    
}