.grid-container {

  margin: 10px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr)); /* Responsive grid with a minimum width of 200px */
  gap: 16px; /* Adjust the gap between grid items as needed */
}

.grid-item {
  color: #fff;
  padding: 16px;
  height: 300px;
  text-align: center;
  cursor: pointer;
}

