.home {
    width: 100%;
    height: 100vh;
    background-image: url("../assets/home.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
    
}
body {
    background-color: rgb(186, 226, 213);
}
.homecontent {
    text-align: start;
    margin-left: 10%;
    margin-top: 50px;
    color: rgb(34, 34, 96);

}
.para {
    text-align: start;
    margin-left: 15%;
    margin-top: 80px;
    font-size: 30px;
    font-weight: 500;
}

@media (width < 600px) {
    .home {
        width: 100%;
        height: 100vh;
        background-image: url("../assets/Homebg.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        
    
    }
    .para {
        font-size: 18px;
    }
}


